.contact-container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .contact-heading {
    font-size: 3.5rem;
    color: var(--primary-color);
    text-shadow: 2px 2px var(--secondary-color);
    margin: 4rem auto;
  }
  
  .company-info {
    text-align: center;
    padding: 12px;
    margin-bottom: 2rem;
    font-size: 1.8rem;
    color: var(--secondary-color);
    text-shadow: 1px 1px var(--primary-color);
  }

  .company-info h3{
    text-align: center;
    padding: 4px;
    margin: 8px;
    
  }

  .company-info p{
    text-align: center;
    padding: 4px;
    margin-bottom: 2rem;
  }
  
  .newsletter-heading {
    font-size: 2.6rem;
    color: var(--primary-color);
    text-shadow: 2px 2px var(--secondary-color);
    margin: 4rem auto;
  }
  
  .newsletter-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 2rem;
    padding: 1rem auto;
    color: var(--secondary-color);
    text-shadow: 1px 1px var(--primary-color);
  }
  
  .newsletter-form div {
    margin: 1.5rem;
  }
  
  label {
    font-weight: bold;
  }
  
  input[type='text'],
  input[type='email'] {
    padding: 1rem;
    border: 1px solid gray;
    border-radius: 3px;
    width: 300px;
  }
  
  .submit-btn {
    margin: 1rem auto;
    border: none;
    font-size: 2.2rem;
    font-weight: 600;
    background-color: transparent; /* Change the background color to transparent */
    color: var(--primary-color);
    text-shadow: 2px 2px var(--secondary-color);
    /* Add font styling or additional styles as needed */
  }
  
  .submit-btn:hover {
    cursor: pointer;
    font-size: 2.3rem;
  }

  /* Media Queries */

@media only screen and (max-width: 768px) {
  /* Adjust styles for smaller screens */
  .contact-heading {
    font-size: 2.8rem;
  }

  .company-info {
    font-size: 1.6rem;
  }

  .newsletter-heading {
    font-size: 2.2rem;
  }

  .newsletter-form {
    font-size: 1.8rem;
  }

  input[type='text'],
  input[type='email'] {
    width: 250px;
  }

  .submit-btn {
    font-size: 2rem;
  }
}

@media only screen and (min-width: 769px) {
  /* Adjust styles for larger screens */
  .contact-heading {
    font-size: 4rem;
  }

  .company-info {
    font-size: 2rem;
  }

  .newsletter-heading {
    font-size: 2.6rem;
  }

  .newsletter-form {
    font-size: 2rem;
  }

  input[type='text'],
  input[type='email'] {
    width: 350px;
  }

  .submit-btn {
    font-size: 2.2rem;
  }
}