:root {
    --primary-light: #fdfdfdf5;
    --primary-color: #f82a1b;
    --secondary-color: #310017;
    --secondary-light: #e3e3e0;
  }
  
  .open-nav-container {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-image: url('https://images.unsplash.com/photo-1567620905732-2d1ec7ab7445?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=480&q=80');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 999;
  }
  
  .open-nav-menu {
    position: absolute;
    top: 4rem;
    right: 4rem;
    padding: 10px;
    font-size: 2.5rem;
    border: none;
    background-color: transparent;
    color: var(--primary-color);
    text-shadow: 2px 2px var(--secondary-color);
    cursor: pointer;
  }
  
  .open-nav-menu:hover {
    font-size: 2.6rem;
    font-weight: 650;
  }  
