
:root {
    --primary-light: #fdfdfdf5;
    --primary-color: #f82a1b;
    --secondary-color: #310017;
    --secondary-light: #e3e3e0;

}
.nav-container {
    margin: 0;
    display: flex;
    position: fixed;
}

.navbar {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100vw;
    background-color: var(--primary-light);
}

.logo-container {
    position: relative;
    margin: 0;
    padding: 4px 0;
    text-decoration: none;
}

.logo-container h2 {
    top: 0;
    left: 0;
    font-size: 1.8rem;
    color: var(--primary-light);
    text-shadow: 3px 3px 3px var(--primary-color);
    /*add different font*/
}

.logo-container h1 {
    font-size: 2rem;
    color: var(--primary-color);
    text-decoration: line-through;
    text-shadow: 2px 2px var(--secondary-color);
}

.logo-container h1, h2:hover {
    cursor: pointer;
}

.order-btn {
    border: none;
    font-size: 1.8rem;
    font-weight: 600;
    background-color: var(--primary-light);
    color: var(--primary-color);
    text-shadow: 2px 2px var(--secondary-color);
    text-decoration: none;
}

.order-btn:hover {
    cursor: pointer;
    font-size: 2rem;
}

.nav-links {
    display: none;
}

.nav-menu {
    margin: 4px;
    padding: 4px; 
    font-size: 2.5rem;
    border: none;
    background-color: rgb(0, 0, 0, 0);
    color: var(--primary-color);
    text-shadow: 3px 3px var(--secondary-color);
}
 .nav-menu:hover {
    cursor: pointer;
    font-size: 2.6rem;
    font-weight: 650;
 }

  
 /* Media Queries */

  
  @media only screen and (min-width: 769px) {
    /* Adjust styles for larger screens */
    .nav-container {
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
    }
  
    .navbar {
      flex-direction: row;
      justify-content: space-around;
      align-items: center;
    }
  
    .logo-container {
      margin-bottom: 0;
    }
  
    .logo-container h2 {
      font-size: 1.8rem;
    }
  
    .order-btn {
      font-size: 1.8rem;
      text-decoration: none;
    }
  
    .nav-links {
      display: flex;
      justify-content: space-around;
      align-items: center;
    }
  
    .nav-menu {
      display: none;
    }
  }
  