:root {
  --primary-light: #fdfdfdf5;
  --primary-color: #f82a1b;
  --secondary-color: #310017;
  --secondary-light: #e3e3e0;

}

.about-section {
  height: 100vh;
  padding: 0;
  margin: 0;
}

.about-container {
  margin: 4rem auto;
  padding: 4rem;
  text-align: center;
  font-size: 2.5rem;
  color: var(--secondary-color);
  text-shadow: 1px 1px var(--primary-color);
}

/* Media Queries */

@media only screen and (max-width: 768px) {
  /* Adjust styles for smaller screens */
  .about-container {
    font-size: 2rem;
  }
}

@media only screen and (min-width: 769px) {
  /* Adjust styles for larger screens */
  .about-container {
    font-size: 3rem;
    max-width: 748px;
  }
}