:root {
    --primary-light: #fdfdfdf5;
    --primary-color: #f82a1b;
    --secondary-color: #310017;
    --secondary-light: #e3e3e0;

}


.footer-section {
    position: fixed;
    bottom: 0;
    margin-top: 3rem;
    padding: .5rem;
    color: var(--primary-light);
    display: flex;
    justify-content: space-evenly;
    align-items: flex-end;
    width: 100vw;
    background-color: var(--primary-color);
}

.social-icons {
    color: var(--primary-light);
    font-size: 1.5em;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    list-style: none;
    gap: 1rem;
}


/* Media Queries */


  @media only screen and (min-width: 769px) {
    /* Adjust styles for larger screens */
    .footer-section {
      flex-direction: row;
    }
  
    .social-icons {
      font-size: 1.5rem;
    }
  } 