:root {
  --primary-light: #fdfdfdf5;
  --primary-color: #f82a1b;
  --secondary-color: #310017;
  --secondary-light: #e3e3e0;

}

/*---------- HERO SECTION ----------- */

body {
    margin: 0;
    background-color: var(--primary-light);
}

  .hero-container {
    overflow: hidden;
    display: flexbox;
    justify-content: space-between;
    align-items: center;
    width: 100vw;
    height: 100vh;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    background-image: url("https://images.unsplash.com/photo-1512621776951-a57141f2eefd?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=870&q=80");
  }

  .hero-text {
    display: flex;
    justify-content: center;
    margin: 8rem 0;
    height: 100%;
    width: 100vw;
    font-size: 3.5rem;
    color: var(--secondary-color);
    text-shadow: 1px 1px var(--primary-color);
  }

  /*---------- OFFER SECTION ----------*/

  .special-offer-section {
    margin: 0;
    padding: 0;
  }

  .special-offer-img {
    height: 100vh;
    background-image: url("https://images.unsplash.com/photo-1547573854-74d2a71d0826?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=870&q=80");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }

  .special-offer-text {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 8rem 0;
    height: 100%;
    width: 100vw;
    font-size: 3.5rem;
    color: var(--secondary-color);
    text-shadow: 1px 1px var(--primary-color);
  }

  .special-offer-btn {
    text-decoration: none;
    position: static;
    margin: 8rem auto;
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    font-size: 2.2rem;
    font-weight: 600;
    background-color: rgb(0, 0, 0, 0);
    color: var(--primary-color);
    text-shadow: 2px 2px var(--secondary-color);
  }

  .special-offer-btn:hover {
    cursor: pointer;
    font-size: 2.3rem;
  }

  /* Media Queries */

@media only screen and (min-width: 769px) {
  /* Adjust styles for larger screens */
  .hero-container {
    flex-direction: row;
  }

  .hero-text {
    font-size: 4rem;
  }

  .special-offer-text {
    font-size: 4rem;
  }

  .special-offer-btn {
    font-size: 4rem;
  }

  .special-offer-btn:hover {
    font-size: 4.1rem;
  }
}