:root {
    --primary-light: #fdfdfdf5;
    --primary-color: #f82a1b;
    --secondary-color: #310017;
    --secondary-light: #e3e3e0;
  
  }
  
  .menu-section {
    height: 100vh;
    padding: 0;
    margin: 0;
  }
  
  .menu-container {
    height: 100vh;
    margin: 0 3rem;
    padding: auto 5rem;
    text-align: center;
    font-size: 2.5rem;
    color: var(--secondary-color);
    text-shadow: 1px 1px var(--primary-color);
  }

  .menu-heading {
    font-size: 4rem;
    margin-bottom: 16px;
  }
  
  .menu-list {
    list-style: none;
    padding: 0;
  }
  
  .menu-item {
    display: flex;
    justify-content: space-between;
    margin-bottom: 8px;
  }
  
  .menu-dish {
    color: var(--secondary-color);
    font-weight: bold;
  }
  
  .menu-price {
    color: var(--secondary-color);
    font-style: italic;
  }

  .menu-order-btn {
    text-decoration: none;
    position: static;
    margin: 8rem auto;
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    font-size: 2.2rem;
    font-weight: 600;
    background-color: rgb(0, 0, 0, 0);
    color: var(--primary-color);
    text-shadow: 2px 2px var(--secondary-color);
  }

  
  .menu-order-btn:hover {
    cursor: pointer;
    font-size: 2.8rem;
  }
  
  
/* Media Queries */

@media only screen and (max-width: 768px) {
  /* Adjust styles for smaller screens */
  .menu-container {
    margin: 0 1rem;
    padding: auto 2rem;
    font-size: 2rem;
  }

  .menu-heading {
    font-size: 3rem;
  }

  .menu-order-btn {
    font-size: 2.2rem;
  }
}

@media only screen and (min-width: 769px) {
  /* Adjust styles for larger screens */
  .menu-container {
    margin: 0 4rem;
    padding: auto 6rem;
    font-size: 2.5rem;
  }

  .menu-heading {
    font-size: 4rem;
  }

  .menu-order-btn {
    font-size: 2.6rem;
  }
}
  
  
  